function createWatcher(watchExpression, listener, state) {
  let value = watchExpression(state);
  listener(value, null, state);

  let oldValue = value;

  return {
    run(newState) {
      const newValue = watchExpression(newState);
      listener(newValue, oldValue, newState);
      // store old value within the closure
      oldValue = newValue;
    }
  };
}

export class State {
  constructor(initial) {
    this.state = initial;
    this.watchers = [];
  }

  watch(watcher, listener) {
    this.watchers.push(createWatcher(watcher, listener, this.state));
  }

  digest() {
    this.watchers.forEach((watcher) => {
      watcher.run(this.state);
    });
  }
}

export const createState = (initialState) => {
  const state = new State(initialState);

  return new Proxy(state, {
    set(target, prop, value) {
      target.state[prop] = value;
      target.digest();

      return true;
    }
  });
};
