import { createState } from "./state";

const appState = createState({
  foo: "hello foo"
});

const el = document.querySelector("#foo");

if (el) {
  appState.watch(
    (state) => state.foo,
    (foo) => {
      el.textContent = foo;
    }
  );

  // now we can update state in event listeners and the UI updates automatically
  el.addEventListener("click", () => {
    appState.foo = "this is another foo";
  });
}
